
button#PushSubscribeButton {
    background: black;
    color: white;
    border: none;
    border-radius: 2px;
    padding: 5px 5px;
    cursor: pointer;
    font-size: 8px;
    position: absolute;
    top: 53px;
    width: 100px;
    right: 10px;
}
.menu-fade-enter-active, .menu-fade-leave-active {
  transition: opacity .3s ease;
}
.menu-fade-enter, .menu-fade-leave-to
/* .component-fade-leave-active for <2.1.8 */ {
  opacity: 0;
}
.mobile-menu {
    background: #232323;
    min-height: 100vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    margin: 0px;
    padding: 0px;
    width: 100%;
    position: fixed;
    color: white;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    list-style: none;
}
.mobile-menu > a {
    font-size: 60px;
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 300;
    font-style: italic;
    color: #fff;
}
.nav-toggle {
  background: transparent;
  border: 1px solid transparent;
  position: relative;
  top: 3px;
  right: 5px;
}
.nav-toggle:focus {
  outline-width: 0;
}
.nav-toggle [class*='bar-'] {
    background: #000000;
    display: block;
    transform: rotate(0deg);
    transition: .2s ease all;
    border-radius: 2px;
    height: 2px;
    width: 40px;
    margin-bottom: 8px;
}
.nav-toggle .bar-bot {
  margin-bottom: 0;
}
.opened .bar-top {
  transform: rotate(45deg);
  transform-origin: 15% 15%;
}
.opened .bar-mid {
  opacity: 0;
}
.opened .bar-bot {
  transform: rotate(-45deg);
  transform-origin: 15% 95%;
}






