
@media (min-width:669px) {
.container.resume-container {
  max-width:704px;
}
.resume-post-content {
  display:-ms-flexbox;
  display:flex;
}
.resume-left {
    margin-right: 30px;
}
}
.resume-section-title {
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 700;
    font-size: 24px;
    font-style: italic;
    line-height: 41px;
    border-top: 2px solid black;
    border-bottom: 2px solid black;
    text-align: center;
    margin-bottom: 14px;
}
.employment-block {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: baseline;
        align-items: baseline;
    margin-bottom: 14px;
}
.employment-year {
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 300;
    font-size: 12px;
    line-height: 14px;
}
.employment-position {
    padding-left: 15px;
}
.employment-location {
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 700;
    font-size: 16px;
    font-style: italic;
    line-height: 20px;
}
.employment-duties {
    font-size: 12px;
    line-height: 21px;
}
.personal-title {
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 700;
    font-size: 16px;
    font-style: italic;
    line-height: 20px;
}



